<template>
  <div style="margin-bottom:320px">

     <h4> Biodata Diri </h4>
     
        <div class="form-group">
          <div v-if="error" class="alert alert-danger" role="alert">
            Data tidak dapat dimuat
          </div>
        </div>


        <div class="form-group" >
          <div v-if="loading" class="alert alert-primary" role="alert">
            Loading..
          </div>
        </div>

        <br>
        <table class="table" v-for="(course, index) in courses" :key="index">
          <tr> 
            <td style="font-weight:800"> Nomor Induk </td>
            <td> {{ course.no_induk }}  </td>
          </tr>
           <tr> 
            <td style="font-weight:800">Nama Lengkap  </td>
            <td> {{ course.nama_lengkap}} </td>
          </tr>
           <tr> 
            <td style="font-weight:800"> Program Studi </td>
            <td> {{ course.program_studi }}   </td>
          </tr>
           <tr> 
            <td style="font-weight:800"> Status </td>
            <td>
            <span class="badge badge-info"> {{ course.role }} </span>
             </td>
          </tr>

           <tr> 
            <td style="font-weight:800"> Tempat / Tanggal Lahir </td>
            <td> {{ course.tempat }} / {{ course.tanggal_lahir }}  </td>
          </tr>

           <tr> 
            <td style="font-weight:800"> Alamat </td>
            <td>  {{ course.alamat }}   </td>
          </tr>
           <tr> 
            <td style="font-weight:800"> Nomor Telpon </td>
            <td>  {{ course.no_telpon }}  </td>
          </tr>
         
        </table>

          <router-link to="/ganti-password" class="btn btn-success">Ganti Password</router-link>

  </div>
 
</template>

<script>
import AutentikasiService from "../services/AutentikasiService";

export default {
  name: "user-profile",
  data() {
    return {
      courses: [],
      currentCourse: null,
      currentIndex: -1,
      title: "",
      loading: false,
      error:false,
    };
  },
  methods: {
    retrieveCourses() {
      this.loading = true;
     AutentikasiService.getProfile()
        .then(response => {
          this.courses = response.data.result;
          console.log(response.data.result);
          this.loading = false;
        })
        .catch(error => {
          this.loading = false;
          this.error = true;  
          this.message =
            (error.response &&
              error.response.data &&
              error.response.data.message) ||
            error.message ||
            error.toString();
        });
    },

    refreshList() {
      this.retrievecourses();
      this.currentCourse = null;
      this.currentIndex = -1;
    },
  },
  mounted() {
    this.retrieveCourses();
  }
};
</script>

<style>
.list {
  text-align: left;
  max-width: 750px;
  margin: auto;
}
</style>
